import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { Query } from "react-apollo";

import asyncImport from '../components/AsyncImport';
import { LOCALLOGIN } from '../graphql/query/local';


const Dashboard = asyncImport(() => import( '../pages/Dashboard'));
const ManagerDashboard = asyncImport(() => import( '../pages/ManagerDashboard'));

class Index extends Component {
    parseJwt(token) {
        var base64Url = token.split('.')[1];
        var base64 = base64Url.replace('-', '+').replace('_', '/');
        return JSON.parse(window.atob(base64));
    }
    render() {        
        return (
            <Query query={LOCALLOGIN}>
                {
                    ({client, loading, error, data })=> {                        
                        if(data.localLogin && data.localLogin.token){
                            const user = this.parseJwt(data.localLogin.token);
                            if(user.role === "0"){
                                return (
                                    <ManagerDashboard {...this.props} client={client} userRole={user.role}></ManagerDashboard>
                                );
                            }
                            return (
                                <Dashboard {...this.props} client={client}></Dashboard>
                            );
                        }
                        else{
                            return (<Redirect to="/login"></Redirect>);
                        }
                    }
                }
            </Query>
        );
    }
}

export default Index;



