import { gql } from 'apollo-boost';

export const LOCALLOGIN = gql`
    query {
        localLogin @client{
            token
        }
    }
`;

export const SITEENTRYTABLE = gql`
    query {
        siteEntryTable @client{
            order,
            orderBy,
            selected,
            page,
            rowsPerPage,
            dialogOpen
        }
    }
`;

export const USERENTRYTABLE = gql`
    query {
        userEntryTable @client{
            order,
            orderBy,
            selected,
            page,
            rowsPerPage,
            dialogOpen
        }
    }
`;



